import React, { useEffect } from "react";
import { Drawer, Button, Spin, Form, Input, Row, Col } from "antd";
import { useProjectModal } from "utils/url";
import { UserSelect } from "../../components/user-select";
import { useAddProject, useEditProject } from "../../utils/project";
import { useForm } from "antd/es/form/Form";
import { ErrorBox } from "../../components/lib";
import { useProjectsQueryKey } from "./util";
import styled from "@emotion/styled";

export const ProjectModal = () => {
  const { projectModalOpen, close, editingProject, isLoading } = useProjectModal();
  const title = editingProject ? "编辑项目" : "创建项目";
  const useMutateProject = editingProject ? useEditProject : useAddProject;
  const { mutateAsync, error, isLoading: mutateLoading } = useMutateProject(useProjectsQueryKey());
  const [form] = useForm();
  const onFinish = (values: { name: string, }) => {
    mutateAsync({ ...editingProject, ...values }).then(() => {
      form.resetFields();
      close();
    });
  };

  const closeModal = () => {
    form.resetFields();
    close();
  }

  useEffect(() => {
    form.setFieldsValue(editingProject);
  }, [editingProject, form]);

  return <Drawer forceRender={true} visible={projectModalOpen} width={"100%"} onClose={closeModal}>
    {
      isLoading ? <Spin size={"large"} style={{position: "fixed", top: '50%', left: '50%'}}/> : <Row justify="center" style={{ minHeight: "100vh" }}>
        <Col>
          <TitleComponent>{title}</TitleComponent>
          <ErrorBox error={error} />
          <Form form={form} layout={"vertical"} style={{ width: "40rem" }} onFinish={onFinish}>
            <Form.Item label={"名称"} name={"name"} rules={[{ required: true, message: "请输入" }]}>
              <Input placeholder={"请输入项目名"} />
            </Form.Item>
            <Form.Item label={"部门"} name={"organization"} rules={[{ required: true, message: "请输入" }]}>
              <Input placeholder={"请输入部门名"} />
            </Form.Item>
            <Form.Item label={"负责人"} name={"personId"}>
              <UserSelect defaultOptionName={"负责人"} />
            </Form.Item>
            <Form.Item>
              <BtnRight>
                <Button loading={isLoading} type={"primary"} htmlType={"submit"}>提交</Button>
              </BtnRight>
            </Form.Item>
          </Form>
        </Col>
      </Row>
    }
  </Drawer>;
};

const TitleComponent = styled.h1`
  text-align: center;
`;

const BtnRight = styled.div`
  text-align: right;
`;
