/**
 * 返回页面url中，指定键的参数值
 */
import { URLSearchParamsInit, useSearchParams } from "react-router-dom";
import { useEffect, useMemo, useRef, useState } from "react";
import { cleanObject, subset } from "./index";
import { useProject } from "./project";

export const useUrlQueryParams = <K extends string>(keys: K[]) => {
  const [searchParams] = useSearchParams();
  const setSearchParams = useSetUrlSearchParam();
  const [stateKeys] = useState(keys);
  return [
    useMemo(
      () => subset(Object.fromEntries(searchParams), stateKeys) as { [key in K]: string },
      // eslint-disable-next-line react-hooks/exhaustive-deps
      [searchParams, stateKeys]),
    (params: Partial<{ [key in K]: unknown }>) => {
      return setSearchParams(params);
    }
  ] as const;
};

/**
 * 返回组件的挂载状态， 如果还没挂载或者已经卸载
 */
export const useMountedRef = () => {
  const mountedRef = useRef(false);
  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });
  return mountedRef;
};

export const useSetUrlSearchParam = () => {
  const [searchParams, setSearchParam] = useSearchParams();
  return (params: { [key in string]: unknown }) => {
    const o = cleanObject({
      ...Object.fromEntries(searchParams),
      ...params
    }) as URLSearchParamsInit;
    return setSearchParam(o);
  };
};


export const useProjectModal = () => {
  const [{ projectCreate }, setProjectCreate] = useUrlQueryParams(["projectCreate"]);
  const [{ editingProjectId }, setEditingProjectId] = useUrlQueryParams(["editingProjectId"]);
  const { data: editingProject, isLoading } = useProject(Number(editingProjectId));
  const open = () => setProjectCreate({ projectCreate: true });
  const setUrlParams = useSetUrlSearchParam();
  const close = () => setUrlParams({ projectCreate: "", editingProjectId: "" });
  const startEdit = (id: number) => setEditingProjectId({ editingProjectId: id });

  return {
    projectModalOpen: projectCreate === "true" || Boolean(editingProjectId),
    open,
    close,
    startEdit,
    editingProject,
    isLoading
  };
};