import React, { useCallback } from "react";
import { useDocumentTitle } from "utils";
import { useKanbans, useRecorderKanban } from "../../utils/kanban";
import {
  useKanbanSearchParams,
  useKanbansQueryKey,
  useProjectInUrl,
  useTaskQueryKey,
  useTasksSearchParams
} from "./util";
import { KanbanColumn } from "./kanban-column";
import styled from "@emotion/styled";
import { SearchPanel } from "./search-pannel";
import { ScreenContainer } from "components/lib";
import { useRecorderTask, useTasks } from "../../utils/task";
import { Spin } from "antd";
import { CreateKanban } from "./create-kanban";
import { TaskModal } from "./task-modal";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Drop, DropChild, Drag } from "../../components/drap-and-drop";

export const KanbanScreen = () => {
  useDocumentTitle("看板列表");
  const { data: kanbans, isLoading: kanbanLoading } = useKanbans(useKanbanSearchParams());
  const { data: currentProject } = useProjectInUrl();
  const { isLoading: taskLoading } = useTasks(useTasksSearchParams());
  const isLoading = taskLoading || kanbanLoading;

  const onDragEnd = useDropEnd();
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <ScreenContainer>
        <h1>{currentProject?.name}看板</h1>
        <SearchPanel />
        {
          isLoading ? <Spin /> : (
            <ColumnsContainer>
              <Drop type={"COLUMN"} direction={"horizontal"} droppableId={"kanban"}>
                <DropChild style={{ display: "flex" }}>

                  {
                    kanbans?.map((kanban, index) => (
                      <Drag key={kanban.id} draggableId={"kanban" + kanban.id} index={index}><KanbanColumn
                        kanban={kanban} /></Drag>))
                  }
                  <CreateKanban />
                </DropChild>
              </Drop>
            </ColumnsContainer>
          )
        }
        <TaskModal />
      </ScreenContainer>
    </DragDropContext>
  );
};

export const useDropEnd = () => {
  const { data: kanbans } = useKanbans(useKanbanSearchParams());
  const { mutate: reorderKanban } = useRecorderKanban(useKanbansQueryKey());
  const { mutate: reorderTask } = useRecorderTask(useTaskQueryKey());
  const { data: allTasks = [] } = useTasks(useTasksSearchParams());
  return useCallback(({ source, destination, type }: DropResult) => {
    if (!destination) {
      return;
    }
    // 看板排序
    if (type === "COLUMN") {
      const fromId = kanbans?.[source.index].id;
      const toId = kanbans?.[destination.index].id;
      if (!fromId || !toId || fromId === toId) {
        return;
      }
      const type = destination.index > source.index ? "after" : "before";
      reorderKanban({ fromId, referenceId: toId, type });
    }

    if (type === "ROW") {
      const fromKanbanId = +source.droppableId;
      const toKanbanId = +destination.droppableId;
      if (fromKanbanId === toKanbanId) {
        return;
      }
      const fromTask = allTasks.filter(task => task.kanbanId === fromKanbanId)[source.index];
      const toTask = allTasks.filter(task => task.kanbanId === toKanbanId)[destination.index];
      if (fromTask?.id === toTask?.id) {
        return;
      }
      reorderTask({
        fromId: fromTask?.id,
        referenceId: toTask?.id,
        fromKanbanId,
        toKanbanId,
        type: fromKanbanId === toKanbanId && destination.index > source.index ? "after" : "before"
      });
    }
  }, [kanbans, reorderKanban, allTasks, reorderTask]);
};

export const ColumnsContainer = styled.div`
  display: flex;
  overflow-x: auto;
  flex: 1;
`;
