import React, { useEffect } from "react";
import { useTaskModal, useTaskQueryKey } from "./util";
import { useForm } from "antd/es/form/Form";
import { useDeleteTask, useEditTask } from "utils/task";
import { Button, Form, Input, Modal } from "antd";
import { UserSelect } from "components/user-select";
import { TaskTypeSelect } from "components/task-type-select";

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 }
};

export const TaskModal = () => {
  const { editingTaskId, editingTask, close } = useTaskModal();
  const [form] = useForm();
  const { mutateAsync: editTask, isLoading: editLoading } = useEditTask(useTaskQueryKey());
  const { mutate: deleteTask } = useDeleteTask(useTaskQueryKey());
  const onDelete = () => {
    close();
    Modal.confirm({
      okText: "确定",
      cancelText: "取消",
      title: "确定删除任务",
      onOk() {
        return deleteTask({ id: Number(editingTaskId) });
      }
    });
  };
  const oncancel = () => {
    form.resetFields();
    close();
  };
  const onOk = async () => {
    await editTask({ ...editingTask, ...form.getFieldsValue() });
    close();
  };

  useEffect(() => {
    form.setFieldsValue(editingTask);
  }, [form, editingTask]);

  return <Modal
    forceRender={true}
    onCancel={oncancel}
    onOk={onOk} okText={"确认"}
    cancelText={"取消"}
    confirmLoading={editLoading}
    title={"编辑任务"}
    visible={!!editingTaskId}
  >
    <Form {...layout} initialValues={editingTask} form={form}>
      <Form.Item label={"任务名"} name={"name"} rules={[{ required: true, message: "请输入任务名" }]}>
        <Input />
      </Form.Item>
      <Form.Item label={"经办人"} name={"processorId"}>
        <UserSelect defaultOptionName={"经办人"} />
      </Form.Item>
      <Form.Item label={"类型"} name={"typeId"}>
        <TaskTypeSelect />
      </Form.Item>
    </Form>
    <div style={{ textAlign: "right" }}>
      <Button onClick={onDelete} style={{ fontSize: "14px" }} size={"small"}>删除</Button>
    </div>
  </Modal>;
};
