import { useHttp } from "./http";
import { QueryKey, useMutation, useQuery } from "react-query";
import { Task } from "../types/task";
import {
  useAddConfig,
  useDeleteConfig,
  useEditConfig,
  useReorderTaskConfig
} from "./use-optimistic-options";

export const useTasks = (param?: Partial<Task>) => {
  const client = useHttp();
  return useQuery<Task[], Error>(["tasks", param], () => client("tasks", { data: param }));
};

// 添加任务
export const useAddTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation((params: Partial<Task>) => client(`tasks`, {
      method: "POST",
      data: params
    }),
    useAddConfig(queryKey)
  );
};

// 编辑任务
export const useEditTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation((params: Partial<Task>) => client(`tasks/${params.id}`, {
      method: "PATCH",
      data: params
    }),
    useEditConfig(queryKey)
  );
};

// 删除任务
export const useDeleteTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(({ id }: { id: number }) => client(`tasks/${id}`, {
      method: "DELETE"
    }),
    useDeleteConfig(queryKey)
  );
};


export interface SortProps {
  fromId: number,
  referenceId: number,
  type: "before" | "after",
  fromKanbanId?: number,
  toKanbanId?: number
}

export const useRecorderTask = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation((params: SortProps) => {
      return client("tasks/reorder", {
        data: params,
        method: "POST"
      });
    },
    useReorderTaskConfig(queryKey)
  );
};