import React, { useEffect, useState } from "react";
import { Card, Input } from "antd";
import { useProjectIdInUrl, useTaskQueryKey } from "./util";
import { useAddTask } from "utils/task";

export interface CreateTaskProps {
  kanbanId: number
}

export const CreateTask = (props: CreateTaskProps) => {
  const { kanbanId } = props;
  const [name, setName] = useState<string>("");
  const [inputMode, setInputMode] = useState<boolean>(false);
  const projectId = useProjectIdInUrl();
  const { mutateAsync: addTask } = useAddTask(useTaskQueryKey());
  const toggle = () => setInputMode((mode) => !mode);
  const submit = async () => {
    await addTask({ name, projectId, kanbanId });
    setInputMode(false);
    setName("");
  };

  useEffect(() => {
    if (!inputMode) {
      setName("");
    }
  }, [inputMode]);

  if (!inputMode) {
    return <div onClick={toggle}>{" "}+创建事务</div>;
  }

  return <Card>
    <Input
      name={name}
      onChange={evt => setName(evt.target.value)}
      onBlur={toggle}
      autoFocus={true}
      onPressEnter={submit}
    />
  </Card>;
};
